html {
  --text-active: #00acdc;
  --text-primary: #000;
  --font-height: 12px;
  --header-height: 60px;
}

body {
  font-family: Roboto, sans-serif;
  font-size: var(--font-height);
  margin: 0;
}

.img {
  height: 40px;
  display: block;
}

.header {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.nav {
  height: var(--header-height);
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  height: var(--header-height);
}

.nav__link {
  height: var(--header-height);
  text-transform: uppercase;
  color: var(--text-primary);
  align-items: center;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link:hover, .is-active {
  color: var(--text-active);
}

.is-active:after {
  content: "";
  background-color: var(--text-active);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
